/**
 * Button
 */
const buttonBG = new THREE.Mesh(
    new THREE.PlaneGeometry(1.2,0.3),
    new THREE.MeshBasicMaterial(
        {color: '#00004d'}
    )
)
buttonBG.position.y = -0.6
scene.add(buttonBG)

//Set the text
const enterText = new Text()
scene.add(enterText)

enterText.text = 'Press \'Enter\' to begin'
enterText.fontSize = 0.11
enterText.position.set(-0.51,-0.53,0.001)
enterText.color = '#ffe6ea'
enterText.outlineBlur = 0.005
enterText.outlineWidth = 0.001
enterText.outlineColor = 'white'

enterText.sync()


/**
 * Info Text
 */
 const infoBG = new THREE.Mesh(
    new THREE.PlaneGeometry(3,2.4),
    new THREE.MeshBasicMaterial(
        {color: '#00004d', opacity: 0.8, transparent: true}
    )
)

infoBG.position.set(7,0.4,0)
//if set on the left, (-6,0.4,0)
scene.add(infoBG)

//Set the text
const infoText = new Text()
scene.add(infoText)

