const iText2 = 'This project is specially created for 360imx at NUS Libraries.\n\n\n\nDeveloped by Stephanie V Budiman\n\nBased on \"Hikayat Panji Semirang\", with script by Raudhah\n\nVoice narration by Mak Jie Ying\n\nAnd Gamelan music performance by Singa Nglaras.'

//infoText.text = iText2
infoText.fontSize = 0.08
//if set on the left, infoText.position.set(-7.1,1.2,0.001)
infoText.position.set(5.9,1.2,0.001)
infoText.maxWidth = 2.2
infoText.textAlign = 'justify'
infoText.color = 'white'


// animating the text
const times = [0, 14, 16, 18, 30, 32, 34, 46, 48]
const opValues = [1, 1, 0, 1, 1, 0, 1, 1, 0]

const opacityKF = new NumberKeyframeTrack('.fillOpacity', times, opValues)

const stringKF = new StringKeyframeTrack(
    '.text',
    [0, 16, 32, 48],
    [iText0, iText1, iText2, iText2])

const tracks = [opacityKF, stringKF]
const length = -1

const textClip = new AnimationClip('textOpacity', length, tracks)
const textMixer = new AnimationMixer(infoText)
const textAction = textMixer.clipAction(textClip)
textAction.play().setLoop(THREE.LoopRepeat)
textAction.clampWhenFinished = true 

infoText.sync()

/**
 * Viewport
 */
 const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera

const camera = new THREE.PerspectiveCamera( 60, sizes.width / sizes.height, 1, 5000  )

camera.position.set(0,0.5,4)
scene.add(camera)


//cubemap
const paths = ['/textures/cube1/','/textures/cube2/','/textures/cube3/','/textures/cube4/']
const format = '.png'

let cubeTextures = []

for (let p in paths)
{
    const urls = [
        paths[p] + 'px' + format, paths[p] + 'nx' + format,
        paths[p] + 'py' + format, paths[p] + 'ny' + format,
        paths[p] + 'pz' + format, paths[p] + 'nz' + format
    ]
    const texture = new THREE.CubeTextureLoader().load( urls )
    cubeTextures.push(texture)
}




// Controls -- enabled for dev and testings, disabled for 360imx build

const controls = new OrbitControls(camera, canvas)
controls.target.set(0,0.5,0)
controls.enableDamping = true
controls.enableZoom = false


/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha: true
})
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.setClearColor( 0x000000, 0 ); 


/**
 * Animate
 */
 const clock = new THREE.Clock()
 let previousTime = 0

 const speed = 0.0002

 var bgIndex, duration, interval
 
 
 const tick = () =>
 {
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime

    //Rotate the background scene
    scene.rotateY(speed)

    //change scene background every 8 seconds
    duration = cubeTextures.length * 8 
    interval = Math.floor(elapsedTime) % duration 
    
    switch (interval)
    {
        case 0:
            bgIndex = 0
            break
        case 8:
            bgIndex = 1
            break
        case 16:
            bgIndex = 2
            break
        case 24:
            bgIndex = 3
            break
    }
    
    if (textMixer)
    {
        textMixer.update(deltaTime) 
    }

    scene.background = cubeTextures[bgIndex]

    // Update controls -- only for dev, it should be disabled for 360imx build
    controls.update()

    document.addEventListener( 'keydown', onKeyDown )

    /*
    //autoplay to the next scene after 15 minutes regardless of user's input
    if (elapsedTime > 900)
    {
        location.replace("https://panji360.github.io/ck_01")
        //location.replace("https://panji360.github.io/ri_00")
    }
    */

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()



function onKeyDown( event ) {
    if (event.keyCode == 13) {
        //location.replace("https://panji360.github.io/ck_01")
        //location.replace("https://panji360.github.io/ri_00")
        location.replace("https://panji360.github.io/gg_01")
    }
}